import { Component, OnInit } from '@angular/core';
import { API } from "../../../custom-components/services/api";

@Component({
  selector: 'ui-tree-demo-basic',
  template: `
    <ui-tree [(ngModel)]="selectNodes" cache="area-tree" (load)="load($event)"
             (onNodeSelect)="onNodeSelect($event)"></ui-tree>
  `,
  styles: []
})
export class UiTreeDemoBasicComponent implements OnInit {
  selectNodes: any[] = [];
  cneeCode: string;
  obj: any;

  constructor(public api: API) {
  }

  ngOnInit() {
  }

  load(event) {
    this.api.call("CommonController.queryAllAreaTree", {
      data: event.data ? event.data : "000000000000"
    }).ok(json => {
      event.children = json.result;
    });
  }

  onNodeSelect($event) {
  }

}



