import { NgModule } from '@angular/core';
import { RouterModule } from "@angular/router";
import { UiTreeDemoComponent } from "app/showcase/custom-components/ui-tree-demo/ui-tree-demo.component";


@NgModule({
	imports: [ RouterModule.forChild([
	  { path: '', component: UiTreeDemoComponent }
	]) ],
	exports: [ RouterModule ]
  })
  export class UiTreeDemoRoutingModule {
  }



