import { CommonModule } from '@angular/common';
import { UiTreeDemoRoutingModule } from "./ui-tree-demo.routing.module";
import { FormsModule } from "@angular/forms";
import { NzGridModule } from "app/showcase/share/nz-grid/nz-grid.module";
import { NzToolTipModule } from "app/showcase/share/nz-tooltip/nz-tooltip.module";
import { NzCodeBoxModule } from "app/showcase/share/nz-codebox/nz-codebox.module";
import { UiTreeDemoBasicComponent } from "./ui-tree-demo-basic.component";
import { UiTreeDemoComponent } from "./ui-tree-demo.component";
import { TreeModule } from "../../../components/tree/tree";
import { UITree } from "../../../custom-components/tree/tree";


@NgModule({
  imports: [
    CommonModule,
    UiTreeDemoRoutingModule,
    FormsModule,
    NzGridModule,
    NzToolTipModule,
    NzCodeBoxModule,
    TreeModule
  ],
  declarations: [
    UiTreeDemoBasicComponent,
    UiTreeDemoComponent,
    UITree
  ]
})
export class UiTreeDemoModule { }



